import React from 'react'
import '../estilos/Navegador.css'
import { Navbar, Container, NavDropdown, Nav} from 'react-bootstrap';
import { useState, useEffect} from 'react';
import logo from '../img/header/robot.jpeg'
import facebook from '../img/header/facebook.png'
import insta from '../img/header/instagram.png'
import wha from '../img/header/whatsapp.png'
const Navegador = () => {


  const {activeLink, setActiveLink} = useState('home');
  const {scrolled,setScrolled} = useState(false);
  useEffect(() =>{
    const onScroll = () => {
      if (window.scrollY >50){
        setScrolled(true);
      }else{
        setScrolled(false);
      }
    }
    window.addEventListener("scroll", onScroll);

    return() => window.removeEventListener("scroll", onScroll);
  }, [])

  const onUpdateActiveLink = (value) =>{
    setActiveLink(value);
  }

  return (
    <Navbar id='navegacion' expand="lg" className={scrolled ? "scrolled":""}>
      <Container>
        <h2 id='Titulo-Nav' >Magdiel</h2>  
        
        <Navbar.Toggle aria-controls="basic-navbar-nav">
          <span className='navbar-toggler-icon'></span>
        </Navbar.Toggle>
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="/pagina-practica1" className={activeLink === 'home'?'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('home')}>Inicio</Nav.Link>
            <Nav.Link href="/informacion" className={activeLink === 'Proyectos'?'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('Proyectos')}>Proyectos</Nav.Link>
            <Nav.Link href="#Habilidades" className={activeLink === 'Habilidades'?'active navbar-link' : 'navbar-link'} onClick={()=> onUpdateActiveLink('Habilidades')}>Habilidades</Nav.Link>
          </Nav>
          <span className='navbar-text'>
            <div className='social-icon'>
              <a href='#'> <img src={facebook} alt=''/></a>
              <a href='#'> <img src={insta} alt=''  /></a>
              <a href='#'> <img src={wha} alt=''  /></a>
            </div>
            <button type='button' className='navbar-toggler' data-bs-toggle="collapse" data-bs-target="#navbarCollapse"> 
              <span className='navbar-toggler-icon'> </span>
            </button>
          </span> 
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default Navegador;
