import Inicio from './components/inicio'

document.addEventListener('DOMContentLoaded', () => {
  const elementosCarousel = document.querySelectorAll('.carousel');
  M.Carousel.init(elementosCarousel, {
    duration: 150,
    dist: 0,
    shift: 5,
    padding: 5,
    numVisible: 1
  });
});

function App() {

  useEffect(() => {
    M.AutoInit();
  }, []);

  return (
    <div className="App">
        <BrowserRouter>
          <Navegador/>
          <Routes>
            <Route exact path="/pagina-practica1" element={<Inicio/>} />
            <Route exact path="/informacion" element={<Info/>} />
          </Routes>
        </BrowserRouter>
      
    </div>
  );
}

export default App;
