import React from 'react'
import '../estilos/inicio.css'
import fondo from '../img/fondo.jpeg'
import robot from '../img/robot-pag.png'
import piso from '../img/piso-robot.png'
const Inicio = () => {
  return (
    <div className='inicio'>
        <div id='contenedor-fondo'>
            <div id='opacidad'>
                <div id='o-texto'>
                    <div id='o-texto-'>
                        <p id='o-texto-p'>
                            Mi Portafolio de Proyectos
                        </p>
                    </div>
                </div>
            </div>
            <div id='opacidad-1'>
                <img src={robot} alt='robot' id='fondo-pantalla' />
                <div id='sombra'></div>
            </div>
        </div>
        <div id='degradado'>
        </div>
        <div id='contenedor-proyectos'>
            <div id='titulo-proyecto'>
                <h1 id='titulo-proyecto-1'>Proyectos</h1>
            </div>
            <div className='contenedores'>
                <div id='contenedores-f'>
                    <div className='contenedores-proyectos' ></div>
                    <div className='contenedores-proyectos' ></div>
                    <div className='contenedores-proyectos' ></div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Inicio
